const container = document.getElementById('root');
const ajax = new XMLHttpRequest(); // letÏùÄ Îã§Î•∏ Í∞í ÎÑ£ÏùÑ Ïàò ÏûàÏßÄÎßå(Î≥ÄÏàò), constÎäî Î∞îÍøÄ Ïàò ÏóÜÎã§.(ÏÉÅÏàò) 
const content = document.createElement('div');
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';
const store = {
  currentPage: 1,
};

// APIÎ°úÎ∂ÄÌÑ∞ Îç∞Ïù¥ÌÑ∞Î•º Î∞õÏïÑ Î∞òÌôòÌïòÎäî Ìï®Ïàò
function getData(url) {
  ajax.open('GET', url, false); // ÎèôÍ∏∞Ï†ÅÏúºÎ°ú Ï≤òÎ¶¨ÌïòÍ≤†Îã§ trueÎäî ÎπÑÎèôÍ∏∞
  ajax.send(); // Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò®Îã§.

  return JSON.parse(ajax.response); // jsonÏùÑ Í∞ùÏ≤¥Ìôî
}

// Í∏Ä Î™©Î°ù ÌôîÎ©¥
function newsFeed() {
  const newsFeed = getData(NEWS_URL);
  const newsList = []; // <li> Ïó¨Îü¨Í∞úÎ•º ÎÑ£ÎäîÎã§

  newsList.push('<ul>');

  for(let i = (store.currentPage - 1) * 10; i < store.currentPage * 10; i++) {
    newsList.push(`
      <li>
        <a href="#/show/${newsFeed[i].id}">
          ${newsFeed[i].title} (${newsFeed[i].comments_count})
        </a>
      </li>
    `);
  }

  newsList.push('</ul>');

  // ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò
  newsList.push(`
    <div>
      <a href="#/page/${store.currentPage > 1 ? store.currentPage - 1 : 1}">Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ</a>
      <a href="#/page/${store.currentPage + 1}">Îã§Ïùå ÌéòÏù¥ÏßÄ</a>
    </div>
  `);

  // Î∞∞Ïó¥ÏùÑ ÌïòÎÇòÏùò Î¨∏ÏûêÏó¥Î°ú ÎßåÎì§Ïñ¥ innerHTMLÎ°ú ÎÑ£Ïñ¥Ï§ÄÎã§
  container.innerHTML = newsList.join('');
}

// Îâ¥Ïä§ ÎÇ¥Ïö© Ìï®Ïàò
function newsDetail() {
  // console.log(location.hash) // #/show/29586970
  const id = location.hash.substr(7); // 29586970
  const newsContent = getData(CONTENT_URL.replace('@id', id));

  container.innerHTML = `
    <h1>${newsContent.title}</h1>
    <div>
      <a href="#/page/${store.currentPage}">Î™©Î°ùÏúºÎ°ú</a>
    </div>
  `;
}

// Í∏Ä Î™©Î°ù ÌôîÎ©¥ÏúºÎ°ú ÎèåÏïÑÍ∞ÄÎäî ÎùºÏö∞ÌÑ∞
function router() {
  const routePath = location.hash; // #/page/2

  if (routePath === '') {
    newsFeed();            // Î¶¨Ïä§Ìä∏ Ìò∏Ï∂ú
  } else if (routePath.indexOf('#/page/') >= 0){  // indexOf : -1Ïù¥Î©¥ ÏûÖÎ†•ÏúºÎ°ú Ï£ºÏñ¥ÏßÑ Î¨∏ÏûêÏó¥ ÏóÜÏùå
    store.currentPage = Number(routePath.substr(7)); // Î¨∏ÏûêÏó¥Ïù¥Îùº '2' + '1 'Ïù¥ ÎêòÏñ¥Î≤ÑÎ¶º
    newsFeed();        
  } else {
    newsDetail(); // Îâ¥Ïä§ÎÇ¥Ïö© Ìò∏Ï∂ú
  }
}

window.addEventListener('hashchange', router);

router();